import jsPDF from 'jspdf';
import html2pdf from 'html2pdf.js';
import {TextUtility} from './Utilities'
import {TextTemplate, MedicalOrderSheetImageCombined} from './Templates';
import { PDFDocument, rgb } from 'pdf-lib';
//const { PDFDocument, StandardFonts, rgb } = PDFLib



export class PdfGenerator {
    constructor(patientNotes, signature, name) {
        this.patientNotes = patientNotes;
        this.signature = signature;
        this.name = name;

        this.jspdfOpt3 = {
            jsPDF: { unit: 'in', format: 'letter', orientation: 'portrait' },
            margin: [0.5, 0.0, 0.5, 0.0],
            pagebreak: { mode: 'avoid-all'}
        }
    }

    getOrderText(text) {
        text = text.replace(/<br\s*[\/]?>/gi, "\n")
        text = text.replace(/<p[^>]*>/g, '').replace(/<\/p>/g, '\n');
        text = new TextUtility().stripHtmlTags(text) 

        
        const regex = /^[P|p]lan[:]?([\s\S]*?)\n\n/smg;
        let m = regex.exec(text);
        if(m && m[1]) {
            return m[1]
        }
        else
            return ""
    }


    async combinePdf() {
        
        let d = new Date()
        let docs = []
        let order = ""
        let physicianName = this.name? this.name:'Steven Wesley';
        let templateTime = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"][d.getMonth()] +"-" + d.getDate() + "-" + d.getFullYear();

        this.patientNotes.forEach(patientNote => {
            let pdfWorker = (new html2pdf.Worker)
            order = this.getOrderText(patientNote.note.text).split(/(?:\r\n|\r|\n)/g).filter((item) => item != "")
            pdfWorker = pdfWorker.from(
                new TextTemplate().generateFromDict({
                    note: patientNote.note.text.replace(/{{signature}}/gm, this.signature)
                })
            ).toContainer()
            .set(this.jspdfOpt3).toPdf().get('pdf')
            .then( (pdf) => {
              this.pdfHeader(pdf, patientNote.patient.name, d)
            }).toPdf()
            .from(
                new MedicalOrderSheetImageCombined().generateFromDict({
                    datetime: templateTime, 
                    lastname: patientNote.patient.name.split(/[, ]+/)[0], 
                    firstname: patientNote.patient.name.split(/[, ]+/)[1], 
                    order0: order.slice(0,5).join("<br/>"),
                    order1: order.slice(5,10).join("<br/>"), 
                    physicianName: physicianName,
                    physicianName1: order.slice(5,10).length > 0? physicianName: "",
                    signature: this.signature,
                    signature1: order.slice(5,10).length > 0? this.signature: "",
                    datetime1: order.slice(5,10).length > 0? templateTime: "",
                    backgroundimage: require("./Assets/MedicalOrderSheet-2.jpg"),
                    logo: require("./Assets/ontario.jpg")
                })
            ).toContainer().set(this.jspdfOpt3).toCanvas().toPdf()            
            docs.push(pdfWorker)
        })
        const f = async () => {
            let buffers = []

            for(let i = 0; i < docs.length; i++){
                let pdf = await docs[i].get("pdf")
                let buffer = await this.elementToPdfBuffer(pdf)
                buffers.push(buffer)
            }

            let doc = await this.mergePdfs(buffers)
            const pdfUrl = URL.createObjectURL(
                new Blob([doc], { type: 'application/pdf' }),
              );
              window.location.assign(pdfUrl);

        }
        f();
    }

    async elementToPdfBuffer(pdf) {
        const pdfBuffer = await pdf.output("arraybuffer");
        return pdfBuffer;
      }

    async mergePdfs(pdfsToMerges) {
        const mergedPdf = await PDFDocument.create();
        const actions = pdfsToMerges.map(async pdfBuffer => {
          const pdf = await PDFDocument.load(pdfBuffer);
          const copiedPages = await mergedPdf.copyPages(pdf, pdf.getPageIndices());
          copiedPages.forEach((page) => {
            // console.log('page', page.getWidth(), page.getHeight());
            //page.setWidth(210);
            mergedPdf.addPage(page);
          });
        });
        await Promise.all(actions);
        const mergedPdfFile = await mergedPdf.save();
        return mergedPdfFile;
      }

    pdfHeader(pdf, patient, date) {
      console.log(pdf.internal)
      var totalPages = pdf.internal.getNumberOfPages();

      for (var i = 1; i <= totalPages; i++) {
          pdf.setPage(i);
          pdf.setFontSize(11);
          pdf.setTextColor(0);
          
          pdf.text("Name: " + patient, 0.2, 0.3);
          pdf.text('DOB ________________ OTIS #: _______________________', 2.8, 0.3);
          pdf.text("Date: " + date.toLocaleDateString(), 7.2, 0.3);

          pdf.text(' Page ' + i + ' of ' + totalPages, 4, pdf.internal.pageSize.getHeight()-0.3);
      }         

      pdf.addPage();
    }
}